<!-- 管理用户 -->

<template>
    <div>
        <el-table :data="users">
            <el-table-column label="昵称" prop="name"></el-table-column>
            <el-table-column label="创建时间" prop="day"></el-table-column>
            <el-table-column label="禁言时间" prop="banday"></el-table-column>
            <el-table-column fixed="right" label="操作" width="600">
                <template slot-scope="scope">
                    <el-button @click="selectVideoClick(scope.row)" type="text" size="small">查看视频评论</el-button>
                    <el-button @click="selectBlogClick(scope.row)" type="text" size="small">查看博客评论</el-button>
                    <el-button @click="showDialog(scope.row)" type="text" size="small">禁言</el-button>
                    <el-button @click="delectClick(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>


        <el-dialog title="选择禁言时间" :visible.sync="dialogFormVisible">
            <el-form :model="form">
                <el-col :span="11">
                    <el-date-picker type="date" placeholder="选择日期" v-model="form.banday"
                        style="width: 100%;"></el-date-picker>
                </el-col>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="banClick()">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="该用户评论" :visible.sync="dialogTableVisible" >
            <el-table :data="gridData">
                <el-table-column property="date" label="日期" width="300"></el-table-column>
                <el-table-column property="text" label="内容"></el-table-column>
                <el-table-column fixed="right" label="操作" width="200">
                    <template slot-scope="scope">
                        <el-button @click="deleteComment(scope.row)" type="text" size="small">删除</el-button>
                        
                    </template>
                </el-table-column>
            </el-table>
            <el-button @click="dialogTableVisible=false">取 消</el-button>
        </el-dialog>
    </div>
</template>

<script>
import axios from 'axios';
const options = {
    inject: ['reload'],
    data() {
        return {
            users: [],
            dialogFormVisible: false,
            dialogTableVisible:false,
            form: {
                id: "",
                banday: ""
            },
            gridData: [],
            commentForm: {
                type: 0,
                id: 0
            }
        }

    },
    mounted: function () {
        this.getUsers()
    },
    methods: {

        selectVideoClick(row) {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/comment/selectV?name="+row.name,
                method: 'get',
                headers: { 'Content-Type': 'application/json' }
            })
                .then(res => {

                    this.gridData = res.data

                });
            this.dialogTableVisible = true;

        },
        selectBlogClick(row) {

            this.commentForm.type = 1;
            axios.request({
                baseURL: "http://localhost:8090",
                url: "/comment/selectB?name="+row.name,
                method: 'get',
                headers: { 'Content-Type': 'application/json' }
            })
                .then(res => {

                    this.gridData = res.data

                });
            this.dialogTableVisible = true;

        },
        deleteComment(row) {
            this.$confirm('此操作将永久删除该评论, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.commentForm.id = row.id;
                axios.request({
                    baseURL: "http://localhost:8090/",
                    url: "/comment/delete?type="+this.commentForm.type+"&id="+this.commentForm.id,
                    method: 'get',
                    headers: { 'Content-Type': 'application/json'}
                })
                    .then(res => {
                        if (res.data) {
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.reload();
                        } else {
                            this.$message({
                                type: 'fail',
                                message: '删除失败!'
                            });
                        }
                    })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });

        },
        banClick() {

            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/user/update",
                data: this.form,
                method: 'post',
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    if (res.data) {
                        alert("禁言成功");
                        this.reload()
                    } else {
                        alert("禁言失败")
                    }

                });

            this.dialogFormVisible = false
        },
        showDialog(row) {
            this.dialogFormVisible = true;
            this.form.id = row.id;
        },

        delectClick(row) {
            this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                axios.request({
                    baseURL: "http://localhost:8090/",
                    url: "/user/delete",
                    data: row.id,
                    method: 'post',
                    headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
                })
                    .then(res => {
                        if (res.data) {
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.reload();
                        } else {
                            this.$message({
                                type: 'fail',
                                message: '删除失败!'
                            });
                        }
                    })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });


        },
        getUsers() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/user/select",
                method: 'post',
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    this.users = res.data

                })
        }
    }

}
export default options;

</script>